import axios from 'axios'
import router from '@/router'
import { storage } from '@/util'
import { Toast } from 'mint-ui'

// Base Url
axios.defaults.baseURL = config.baseUrl

// Token
axios.interceptors.request.use(config => {
  config.headers['Token'] = storage.getToken()
  return config
})

// Token
axios.interceptors.response.use(response => {
  if (response.data != null) {
    storage.setToken(response.data.token)
  }
  return response
})

// status
axios.interceptors.response.use(response => {
  if (typeof response.data.status !== 'number') {
    return Promise.reject({
      message: ''
    })
  }

  if (response.data.status !== 1) {
    if (response.data.error != null) {
      return Promise.reject(response.data.error)
    } else {
      return Promise.reject({
        message: '1'
      })
    }
  }
  return response.data.result
})

// 
axios.interceptors.response.use(undefined, error => {
  const response = error.response

  if (response != null) {
    // 
    if (response.status === 401) {
      router.replace({
        path: '/login',
        query: { redirect: router.currentRoute.fullPath }
      })
      return Promise.reject({
        handled: true,
        message: ''
      })
    }

    // Token
    if (response.status === 403) {
      return Promise.reject({
        message: ''
      })
    }

    // 
    if (response.data != null && response.data.error != null) {
      return Promise.reject(response.data.error)
    }
  }

  // 
  return Promise.reject(error)
})

function sendRequest(method, url, data) {
  return axios({ method, url, data }).catch(error => {
    // 
    if (!error.handled) {
      if (error.message != null) {
        Toast({
          message: error.message
        })
      } else {
        Toast({
          message: ''
        })
      }
    }
    throw error
  })
}

function get(url, params) {
  return sendRequest('get', url, params)
}

function post(url, data) {
  return sendRequest('post', url, data)
}

const api = {
  // 
  checkAutoLogin(code) {
    return post('/alliances/CheckOpenID', {
      AllianceInfo: {
        token: code
      }
    })
  },
  // 
  loginByWx(code) {
    return post('/alliances/AllianceLogin', {
      AllianceInfo: {
        token: code
      }
    })
  },
  //
  getUserInfo() {
    return post('/alliances/AllianceLogin', {})
  },
  // 
  sendVerifyCode(mobile) {
    return post('/alliances/SendRegCode', {
      AllianceInfo: {
        mobile
      }
    })
  },
  // 
  register(mobile, verifyCode) {
    return post('/alliances/RegNewAlliance', {
      AllianceInfo: {
        mobile,
        regcode: verifyCode
      }
    })
  },
  // 
  getRegions() {
    return post('/regions/RequestRegions')
  },
  // 
  getLicenseList(city) {
    return post('/services/RequestService', {
      serviceInfo: {
        regionId: city.regionId
      }
    })
  },
  // 
  getPurchasePackages(city, purchases) {
    return post('/services/ApplyBuyService', {
      ServiceInfo: {
        regionId: city.regionId,
        purchases: purchases
      }
    })
  },
  getCustomerList(city, license) {
    const req = {
      regionId: city.regionId
    }
    if (license != null) {
      req.serviceId = license.id
    }
    return post('/alliances/RequestAllianceStore', {
      AllianceInfo: req
    })
  },
  confirmPurchase(city, purchases, packages, totalPrice) {
    return post('/pays/allianceBuylisence', {
      ServiceInfo: {
        regionId: city.regionId,
        buyList: purchases,
        packageList: packages,
        orderMoney: totalPrice
      }
    })
  },
  commitOrder(id) {
    return post('/services/CommitOrder', {
      ServiceInfo: {
        orderId: id
      }
    })
  },
  searchShop(city, keyword) {
    return post('/stores/RequestStoreFromBrandName', {
      brandInfo: {
        regionId: city.regionId,
        name: keyword
      }
    })
  },
  getShopLicenses(city, shop) {
    return post('/stores/RequestStoreService', {
      storeInfo: {
        regionId: city.regionId,
        storeId: shop.id
      }
    })
  },
  authorizeShopLicenses(city, shop, licenses, price) {
    return post('/stores/StoreBuyService', {
      storeInfo: {
        regionId: city.regionId,
        storeId: shop.id,
        serviceList: licenses,
        money: price
      }
    })
  },
  // 
  calcPackage(regionId, purchases) {
    return post('/services/ApplyBuyService1', {
      ServiceInfo: {
        regionId,
        purchases
      }
    })
  },
  someGetMethod() {
    return get('someGetMethod')
  },
  //  
  getOrders() {
    return post('/alliances/RequestAllianceOrders')
  },
  // 
  getOrderDetail(id, type) {
    return post('/alliances/RequestAllianceOrderDetail', {
      orderInfo: {
        orderId: id,
        type: type
      }
    })
  },
  // 
  modifyName(name) {
    return post('/alliances/modifyAllianceName', {
      AllianceInfo: {
        name: name
      }
    })
  },
  // 
  modifyMobile(mobile, regcode) {
    return post('/alliances/modifyAllianceMobile', {
      AllianceInfo: { mobile, regcode }
    })
  },
  // 
  getMessages() {
    return post('/Notifies/RequestNotify')
  },
  //  
  wxConfig(url) {
    return post('/pays/GetTicket', { GetTicket: { url: url } })
  },
  // 
  getOrderStatus(orderId) {
    return post('/pays/GetOrderPayStatus', { GetOrderStatus: { orderid: orderId } })
  },
  // 
  requestServiceStores(regionId, serviceId) {
    return post('/stores/RequestRegionService', {
      storeInfo: { serviceId, regionId }
    })
  },
  // 
  requestStoreInfo(storeId) {
    return post('/stores/RequestStoreInfo', { storeInfo: { storeId } })
  },
  // cancel
  cancelOrder (orderid) {
    return post('/pays/CancelOrder', { RegInfo: { orderid } })
  }
}

export default class Api {
  static install(Vue) {
    Object.defineProperty(Vue.prototype, '$api', {
      get() { return api }
    })
  }
}



