<template title="">
  <div class="ProfileDetail">
    <mt-field label="" v-model="name" placeholder=""></mt-field>
    <div class="join-p btn">
      <mt-button type="primary" size="large" @click="changeName()">
        
      </mt-button>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      name: ''
    }
  },
  methods: {
    async changeName() {
      const result = await this.$api.modifyName(this.name)
      this.$router.go(-1)

    }
  },
  mounted() {
    this.name = this.$route.query.name;
  }
}
</script>

<style >
.btn {
  padding-top: 40px;
}
</style>



