<template title="">
  <div class="TradeLogDetail">
    <div class="page-part">
      <mt-cell title="" class="orderPrice">
        <span v-if="order.type !== 'allianceBuy'" class="income join-p-tb">+{{order.paymoney | money}}</span>
        <span v-if="order.type === 'allianceBuy'" class="cost">-{{order.paymoney | money}}</span>
      </mt-cell>
      <mt-cell title="" :value="payFor"></mt-cell>
      <mt-cell title="" v-if="order.status != 0" :value="orderStatus(order.status)"></mt-cell>
      <mt-cell title="" :value="order.addtime | date('yyyy-MM-dd HH:MM:ss')"></mt-cell>
      <mt-cell title="" :value="order.orderid"></mt-cell>
    </div>
    <div class="list-title join-p-lr">
      <i class="icon"></i></div>
      <div class="servicelist" v-if="order.type === 'allianceBuy'">
        <mt-cell  v-for="(item,index) in detail.buyContext" :key="index"   :title="item.serviceName" :value="`${item.quantity || item.newQuantity} | ${item.autoEndDate?item.autoEndDate+'':item.month+''}`">
        </mt-cell>

      </div>
      <div class="priceList" v-if="order.type === 'allianceBuy'">
        <mt-cell  v-for="(packge,index) in detail.packgeContext" :key="index" :title="`${packge.packgeTime || packge.month} | ${packge.packgeName || packge.lisenceCount+''}`">
          <span class="amount">x1</span>
          <span class="unitPrice">{{packge.packgeMoney | money}}</span>
        </mt-cell>
      </div>

    <div class="priceList" v-if="order.type === 'storeBuy'">
      <mt-cell v-for="(item,index) in detail" :key="index" :title="item.title">
        <!--<span class="amount">x1</span>-->
        <span class="unitPrice">{{item.paymoney | money}}</span>
      </mt-cell>

    </div>

  </div>
</template>
<script>
export default {
  data() {
    return {
      order: {},
      detail:{}
    }
  },
  computed: {
    payFor () {
      if (Object.keys(this.order).length === 0) {
        return ''
      }
      if (this.order.title.indexOf('') > -1) {
        return this.order.city + ''
      } else if (this.order.title.indexOf('')) {
        return this.order.city + ''
      }
      return this.order.title || ''
    }
  },
  methods: {
    orderStatus (type) {
      if (type === 'new') {
        return ''
      } else if (type === 'timeout') {
        return ''
      } else if (type === 'commit') {
        return ''
      } else {
        return ''
      }
    }
  },
  async mounted() {
    let order = this.$route.params
    this.order = order
    const result = await this.$api.getOrderDetail(order.orderid, order.type)
    this.detail = result
  }
}
</script>
<style lang="scss">

</style>



