<template title="">
  <div class="TradeLogs">
    <div class="list-wrap">
      <div class="list-title">
        <i class="icon"></i> {{tradeLogs.length }}</div>

    </div>
    <mt-cell v-for="(trade,index) in tradeLogs" :key="index" :title="trade.city+(trade.type==='allianceBuy'?'-':'')" @click.native="goToDetail(trade)" :label="trade.addtime | date('yyyy-MM-dd HH:MM:ss')" is-link>
        <span :class="{'income':trade.type !== 'allianceBuy'}">{{priceStr(trade)}}</span>
      </mt-cell>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tradeLogs: []
    }
  },
  methods: {
    priceStr (trade) {
      if (trade.status === 'timeout') {
        return ''
      }
      if (trade.status === 'new') {
        return ''
      }
      if (trade.type === 'allianceBuy') {
        return '-' + trade.paymoney.toFixed(2)
      }
      return '+' + trade.paymoney.toFixed(2)
    },
    goToDetail(order) {
      this.$router.push({ name: 'trade-log-detail', params: order })
    }
  },
  async mounted() {
    this.tradeLogs = await this.$api.getOrders()
  }
}
</script>



