import wx from 'weixin-js-sdk'
import axios from 'axios'

// wxConfig(location.href)
export default {
  shareToFriend(obj, success, cancel) {
    wx.onMenuShareAppMessage({
      title: obj.title, // 
      desc: obj.desc, // 
      link: obj.url, // 
      imgUrl: 'http://car.eshine.cn/logo88.png', // 
      type: 'link', // ,musicvideolinklink
      dataUrl: '', // typemusicvideo
      success: function() {
        // 
        if (success) {
          success()
        }
      },
      cancel: function() {
        // 
        if (cancel) {
          cancel()
        }
      }
    })
  },
  wxConfig: wxConfig
}

function wxConfig(url) {
  var params = { GetTicket: { url: url } }
  axios.post(config.baseUrl + 'Visitors/GetTicket/', params)
    .then(data => {
      if (data && data.status === 1) {
        var timeStamp = data.result.timestamp + ''
        var jsTk = data.result.signature
        // var url = data.result.url
        var appId = data.result.appid
        var nStr = data.result.noncestr
        var wxConfig = {
          debug: false, // ,apialertpclogpc
          timestamp: timeStamp, // 
          nonceStr: nStr, // 
          appId: appId,
          signature: jsTk, // 1 jsapitk
          jsApiList: ['hideMenuItems', 'onMenuShareTimeline', 'onMenuShareAppMessage'] // JSJS2
        }
        // console.log(wxConfig)
        wx.config(wxConfig)
        wx.error(function(res) {
          console.log('res')
        })
      }
    })
}



